import { useState, useEffect } from "react";

//dependencies
import { useNavigate } from "react-router-dom";
import useUser from "../../hooks/useUser"
//MaterialUI
import Avatar from "@material-ui/core/Avatar";
import Button from "@material-ui/core/Button";
import CssBaseline from "@material-ui/core/CssBaseline";
import TextField from "@material-ui/core/TextField";
import Link from "@material-ui/core/Link";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";
import Notification from "../Notification";
import Swal from "sweetalert2";
import { LazyLoadImage } from "react-lazy-load-image-component";
const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    backgroundColor: 'white',
    color:'black',
    padding:'30px',
    borderRadius:'15px',  
    boxShadow:'5px 5px 10px #222261'

  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: 'dark',
    width:'80px',
    height:'80px',
    border:'2px solid #4949a5'
  },
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing(1),   
    
  },
  texto: {

    backgroundColor: theme.palette.common.white,
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
    padding:'12px 0'
  },
  
}));

/**
 * Función para mostrar componente de login y hacer la validación de login con el backend.
 * @param {boolean} param0 
 * @returns redirecciona al index si es correcto el login, de lo contrario muestra mensaje de error.
 */
export default function SignIn({onLogin}) {
  const history = useNavigate();
  const {isLoginLoading, hasLoginError, login, isLogged, errLogin} = useUser()
  const [errorMessage, setErrorMessage] = useState("");
  const initialFormData = Object.freeze({
    email: "",
    password: "",
  });
  const Notificacion = Notification();
  const [formData, updateFormData] = useState(initialFormData);
  const [isButtonDisabled, setIsButtonDisabled] = useState(true);

  /**
   * Verifica cambios en el formulario y agrega los valores al formData
   * @param {*} e 
   */
  const handleChange = (e) => {
    const { name, value } = e.target;
  
    updateFormData((prevData) => {
      const newData = { ...prevData, [name]: value.trim() };
      
      // Habilita el botón solo si ambos campos están llenos
      setIsButtonDisabled(!(newData.email && newData.password));
      
      return newData;
    });
  };

  /**
   * Valida si el login es correcto para redireccionar al index
   */
  useEffect(() => {
    if (isLogged) {
      Swal.fire({
        title: "Bienvenido(a) a SCCOTVIEW",
        html: "Esta plataforma de videos tiene imágenes y contenido sensible, fruto del dedicado esfuerzo de nuestros especialistas. Disfruta de esta herramienta exclusiva de la SCCOT, creada para ti. ¡Gracias por ser parte de nuestra comunidad y a nuestros expositores por su invaluable contribución a la ciencia y la investigación!",
        /* html: "Esta plataforma de videos tiene imágenes y contenido sensible, fruto del dedicado esfuerzo de nuestros especialistas. Disfruta de esta herramienta exclusiva de la SCCOT, creada para ti. ¡Gracias por ser parte de nuestra comunidad y a nuestros expositores por su invaluable contribución a la ciencia y la investigación!", */
        icon: "info",
        confirmButtonText: "Aceptar", // Texto del botón
        confirmButtonColor: "rgb(63, 81, 181)", // Color verde
      }).then(() => {
        history('/');
        onLogin && onLogin();
        window.location.reload();
      });
    }
  }, [history, isLogged, onLogin]);
  

  /**
   * Valida si el login es incorrecto para enviar mensaje de error
   */
  useEffect(() => {
    if (hasLoginError) {
      Swal.fire({
        title: errLogin != '' ? errLogin : "Lo sentimos no fue posible iniciar sesión",
        html: "Puede contactarnos al correo membresias@sccot.org.co <br /> <a href='wa.link/mmff0z'><input type='button' class='btn btn-outline-success' value='WhatsApp' /></a>",
        icon: "error"
      });
      setTimeout(() => {
        setErrorMessage(null);
      }, 5000);
    }
  }, [hasLoginError])
  
  /**
   * Envia los datos del formulario a la función login que está en el hook useUser para consultar con el backend.
   * @param {*} e 
   */
  const handleLogin = (e) => {
    
    e.preventDefault();    
    login({
      username: formData.email,
      password: formData.password,
    })    
  };

  const classes = useStyles();

  return (
    <div className="mt-4">{isLoginLoading && <strong className="d-flex justify-content-center flex-column align-items-center">
      <div className="spinner-grow text-light " role="status">
      </div>
        <span className="sr-only d-inline h4 mt-1">Espera por favor</span>
      </strong>}
      {!isLoginLoading &&
      <Container component="main" maxWidth="xs">

        <CssBaseline />
        <div className={classes.paper}>
          
          <Avatar className={classes.avatar}></Avatar>
          <Typography component="h1" variant="h5">
            Iniciar sesión
          </Typography>
          <form className={classes.form} noValidate>
            <TextField
              className={classes.texto} 
              
              margin="normal"
              required
              fullWidth
              id="email"
              label="Correo Electrónico"
              name="email"
              autoComplete="email"
              autoFocus
              onChange={handleChange}
            />
            <TextField
            className={classes.texto}
              margin="normal"
              required
              fullWidth
              name="password"
              label="Contraseña"
              type="password"
              id="password"
              autoComplete="current-password"
              onChange={handleChange}
            />
            <Button
              type="submit"
              fullWidth
              variant="contained"
              color="primary"
              className={classes.submit}
              onClick={handleLogin}
              disabled={isButtonDisabled}
            >
              Ingresar
            </Button>
            <Grid container>
              <Grid item xs>
                <a 
                  href="javascript:void(0);" 
                  className="cursorPointer text-dark"
                  onClick={() => {
                    Swal.fire({
                      title: errLogin !== '' ? errLogin : "¿Olvidó su contraseña?",
                      html: "Puede contactarnos al correo membresias@sccot.org.co <br /> o contáctenos por <a class='text-success' href='https://wa.link/mmff0z' target='_blank'>WhatsApp</a>",
                      confirmButtonText: 'Entendido',
                      confirmButtonColor:'#3f51b5',
                      icon: "info"
                    });
                  }}
                >
                  ¿Olvidó su contraseña?
                </a>
              </Grid>
            </Grid>

          </form>        
        </div>
      </Container>
       }
       { hasLoginError &&  <Notificacion message={errorMessage} />
       }
      
    </div>
  );
}
